* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: sans-serif;
}
.container {
  width: 100%;
  max-width: 1170px;
  padding: 0 12px;

  margin: 0 auto;
}

.search {
  margin-bottom: 45px;
}
.search-box {
  width: 100%;
  border-radius: 5px;
  background-color: #e8b4b8;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.search-box input {
  flex-grow: 1;
  padding: 14px 22px;
  border-radius: 5px;
  outline: none;
  border: none;
  min-width: 0;
}
.search-btn {
  display: flex;
  align-items: center;
}
.search-btn .search-btn__text {
  margin-left: 2px;
  font-size: 16px;
  line-height: 19px;
}
.search-btn__icon {
  width: 20px;
  height: auto;
}
.content {
  padding-bottom: 50px;
}
.content-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 30px;
}
.content-main {
  flex-grow: 1;
}
.content-side {
  width: 260px;
  min-width: 260px;
  margin-top: 50px;
}
.content-main__title {
  font-size: 24px;
  line-height: 28px;
  margin-top: 0;
  margin-bottom: 25px;
  color: #5c3a3a;
}
.content-main__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 30px;
}

.content-side__title {
  font-size: 18px;
  line-height: 21px;
  margin-top: 0;
  margin-bottom: 23px;
  color: #5c3a3a;
}
.content-side__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  gap: 25px;
  border-bottom: 1px solid #a48f8f;
  padding-bottom: 36px;
  margin-bottom: 36px;
}
.content-side__list-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.content-side__list-item svg {
  color: #a48f8f;
  width: 30px;
  height: 30px;
}
.content-side__list-item--title {
  margin-top: 7px;
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 16px;
  color: #883c3c;
}
.content-side__list-item--text {
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  color: #5c3a3a;
}
.conten-side__footer {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.conten-side__footer--item {
  color: #a48f8f;
  margin: 0;
  font-size: 14px;
  line-height: 16px;
}
.content-product {
  display: flex;
  flex-grow: 1;
  gap: 30px;
}
.content-product__left {
  flex-basis: 50%;
}
.content-product__right {
  flex-basis: 50%;
}
.content-product__img {
  width: 100%;
}
.btn-primary__product {
  width: 70%;
}
@media (max-width: 1200px) {
  .container {
    max-width: 930px;
  }
}

@media (max-width: 991px) {
  .container {
    max-width: 730px;
  }
  .content-box {
    flex-direction: column;
  }
  .content-main {
    width: 100%;
  }
  .content-side {
    width: 100%;
    min-width: none;
  }
  .content-product {
    flex-direction: column;
    width: 100%;
  }
  .content-product__left {
    flex-basis: 100%;
    width: 100%;
  }
  .content-product__right {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
@media (max-width: 786px) {
  .container {
    width: 100%;
  }

  .search-btn .search-btn__text {
    display: none;
  }
}
